//*mixins*//
@import "./mixin.scss";

@mixin waves-transition($transition) {
   -webkit-transition: $transition;
   transition: $transition;
}
@mixin waves-transform($string) {
   -webkit-transform: $string;
   transform: $string;
}
@mixin waves-box-shadow($shadow) {
   box-shadow: $shadow;
}
.waves-effect {
   position: relative;
   cursor: pointer;
   display: inline-block;
   overflow: hidden;
   -webkit-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
   -webkit-tap-highlight-color: transparent;

   .waves-ripple {
      position: absolute;
      border-radius: 50%;
      width: 100px;
      height: 100px;
      margin-top: -50px;
      margin-left: -50px;
      opacity: 0;
      background: rgba(0, 0, 0, 0.6);
      $gradient: rgba(0, 0, 0, 0.2) 0, rgba(0, 0, 0, 0.3) 40%, rgba(0, 0, 0, 0.4) 50%, rgba(0, 0, 0, 0.5) 60%,
         rgba(255, 255, 255, 0) 70%;
      background: radial-gradient($gradient);
      @include waves-transition(all 0.5s ease-out);
      -webkit-transition-property: -webkit-transform, opacity;
      -webkit-transition-property: opacity, -webkit-transform;
      transition-property: opacity, -webkit-transform;
      transition-property: transform, opacity;
      transition-property: transform, opacity, -webkit-transform;
      @include waves-transform(scale(0) translate(0, 0));
      pointer-events: none;
   }

   &.waves-light .waves-ripple {
      background: rgba(255, 255, 255, 0.4);
      $gradient: rgba(228, 163, 163, 0.2) 0, rgba(218, 157, 157, 0.3) 40%, rgba(61, 59, 59, 0.4) 50%,
         rgba(148, 69, 69, 0.5) 60%, rgba(252, 237, 237, 0.404) 70%;
      background: radial-gradient($gradient);
   }
   &.waves-classic .waves-ripple {
      background: rgba(0, 0, 0, 0.2);
   }

   &.waves-classic.waves-light .waves-ripple {
      background: rgba(255, 255, 255, 0.4);
   }
}

.waves-notransition {
   @include waves-transition(none #{""}!important);
}

.waves-button,
.waves-circle {
   @include waves-transform(translateZ(0));
   -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
}

.waves-button,
.waves-button:hover,
.waves-button:visited,
.waves-button-input {
   white-space: nowrap;
   vertical-align: middle;
   cursor: pointer;
   border: none;
   outline: none;
   color: inherit;
   background-color: rgba(0, 0, 0, 0);
   font-size: 1em;
   line-height: 1em;
   text-align: center;
   text-decoration: none;
   z-index: 1;
}

.waves-button {
   padding: 0.85em 1.1em;
   border-radius: 0.2em;
}

.waves-button-input {
   margin: 0;
   padding: 0.85em 1.1em;
}

.waves-input-wrapper {
   border-radius: 0.2em;
   vertical-align: bottom;

   &.waves-button {
      padding: 0;
   }

   .waves-button-input {
      position: relative;
      top: 0;
      left: 0;
      z-index: 1;
   }
}

.waves-circle {
   text-align: center;
   width: 2.5em;
   height: 2.5em;
   line-height: 2.5em;
   border-radius: 50%;
}

.waves-float {
   -webkit-mask-image: none;
   @include waves-box-shadow(0px 1px 1.5px 1px rgba(0, 0, 0, 0.12));
   @include waves-transition(all 300ms);

   &:active {
      @include waves-box-shadow(0px 8px 20px 1px rgba(0, 0, 0, 0.3));
   }
}

.waves-block {
   display: block;
}
